import * as React from "react";
import { Link } from "gatsby";
import { CategoryBadge } from "./CategoryBadge";
import { formatDate, calculateReadTime } from "../utils/date";

interface BlogPostCardProps {
    title: string;
    excerpt: string;
    date: string;
    category: string;
    slug: string;
    html?: string;
}

export const BlogPostCard: React.FC<BlogPostCardProps> = ({
    title,
    excerpt,
    date,
    category,
    slug,
    html
}) => {
    const readTime = html ? calculateReadTime(html) : 5;

    return (
        <article className="card group">
            <div className="flex items-center gap-3 mb-4">
                <CategoryBadge category={category} />
                <span className="text-sm text-[var(--color-text-muted)]">
                    {formatDate(date)}
                </span>
                <span className="text-sm text-[var(--color-text-muted)]">·</span>
                <span className="text-sm text-[var(--color-text-muted)]">
                    {readTime} min read
                </span>
            </div>

            <Link to={slug}>
                <h2 className="text-2xl font-bold font-serif mb-3 text-[var(--color-text)] group-hover:text-[var(--color-accent)] transition-colors">
                    {title}
                </h2>
            </Link>

            <p className="text-[var(--color-text-secondary)] mb-4 line-clamp-3">
                {excerpt}
            </p>

            <Link
                to={slug}
                className="inline-flex items-center text-[var(--color-accent)] font-semibold hover:text-[var(--color-accent-hover)] transition-colors"
            >
                Read more →
            </Link>
        </article>
    );
};
